import React from 'react';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';

export default () => (
  <LazilyLoad
    modules={{ AgentChatTextEditor: () => importLazy(new Promise((resolve, reject) => {
      require.ensure(['./AgentsTab/AgentChatTextEditor'], (require) => {
        resolve(require('./AgentsTab/AgentChatTextEditor'));
      }, 'editor').catch(reject);
    })) }}
    loading={loadingComponent}
  >
    {
      ({ AgentChatTextEditor }) => (
        <AgentChatTextEditor />
      )
    }
  </LazilyLoad>
);



