import { connect } from 'react-redux';
import Immutable from 'immutable';
import _ from 'lodash';
import ChatContent from '../../components/ChatSection/ChatContent/ChatContent';
import * as actions from '../../actions/agentChat';
import { mapToChatContentState } from '../Business/agentChat';
import * as selectors from '../../reducers/selectors';
import { noSelectedForAgent } from '../../constants/enums';
import * as fontSize from '../../constants/enums/fontSize';
import * as audioVideoChatStatus from '../../constants/enums/audioVideoChatStatus';

const mapStateToProps = (state) => {
  if (_.isNull(selectors.getSelectedIdForAgents(state)) ||
    selectors.getSelectedIdForAgents(state) === noSelectedForAgent) {
    return {
      id: noSelectedForAgent,
      selectedFontSize: fontSize.middle,
      chatTo: '',
      typingMessage: '',
      scrollTop: -1,
      messages: Immutable.List(),
      textDirectionIfRTL: selectors.textDirectionIfRTL(state),
      errorMessageForChat: '',
      audioVideoChatStatus: audioVideoChatStatus.notStart,
    };
  }
  return mapToChatContentState(state);
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {
    onScroll(id, scrollTop) {
      dispatch(actions.updateScrollTop(id, scrollTop));
    },
    hideErrorMessage() {
      dispatch(actions.setErrorMessageForAgentChat(stateProps.id, ''));
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(ChatContent);



