import React, { PropTypes } from 'react';
import styles from './agents.css';

const NoAgents = props => (
  <div className={styles.noDataSection}>
    <div>
      <h1>You are alone here :(</h1>
      {'Need more hands? '}
      <span
        onClick={() => props.addMoreAgents(props.siteInfo)}
      >
        {'Add Agents Now.'}
      </span>
    </div>
  </div>
);
NoAgents.propTypes = {
  siteInfo: PropTypes.shape({}).isRequired,
  addMoreAgents: PropTypes.func.isRequired,
};
export default NoAgents;




