import React, { PropTypes, Component } from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import styles from './agents.css';
import Avatar from '../Icons/Avatar';
import KickOff from './KickOff';
import Button from '../Button/Button';

class ChatHeader extends Component {
  constructor(props) {
    super();
    this.avatarLink = _.isEmpty(props.agent.avatarLink) ?
                      '' :
                      `${props.agent.avatarLink}&${Date.now()}`;
  }
  componentWillReceiveProps(nextProps) {
    if (_.isEmpty(nextProps.agent.avatarLink)) {
      this.avatarLink = '';
      return;
    }
    this.avatarLink = `${nextProps.agent.avatarLink}&${Date.now()}`;
  }
  shouldComponentUpdate(nextProps) {
    return this.props.agent.avatarLink !== nextProps.agent.avatarLink ||
           this.props.agent.name !== nextProps.agent.name ||
           this.props.agent.ifDisableKickOff !== nextProps.agent.ifDisableKickOff ||
           this.props.agent.ifDisableSetAway !== nextProps.agent.ifDisableSetAway;
  }
  render() {
    return (
      <div>
        <div className={styles.avatar}>
          <Avatar
            avatarLink={this.avatarLink}
          />
          <span>{this.props.agent.name}</span>
        </div>
        <div
          className={styles.toolbar}
        >
          <KickOff
            onKickOff={() => this.props.onKickOff(this.props.agent)}
            className={styles.commonbtn}
            disabled={this.props.agent.ifDisableKickOff}
          />
          <Button
            text="Set Away"
            type="default"
            disabled={this.props.agent.ifDisableSetAway}
            onClick={() => this.props.onSetAway(this.props.agent)}
            className={classnames(styles.commonbtn,
                        this.props.agent.ifDisableSetAway && styles.disable)}
          />
        </div>
      </div>
    );
  }
}

ChatHeader.propTypes = {
  agent: PropTypes.shape({
    avatarLink: PropTypes.string,
    name: PropTypes.string,
    ifDisableKickOff: PropTypes.bool,
    ifDisableSetAway: PropTypes.bool,
  }).isRequired,
  onKickOff: PropTypes.func.isRequired,
  onSetAway: PropTypes.func.isRequired,
};
ChatHeader.examples = {
  'Default Example': {
    description: 'Agents Chat',
    component:
      <ChatHeader />
        ,
  },
};
export default ChatHeader;



