import _ from 'lodash';
import React from 'react';
import classnames from 'classnames';
import styles from './agents.css';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';
import PromptNumber from '../Icons/PromptNumber';

class Item extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(nextProps.agent, this.props.agent);
  }
  render() {
    return (
      <div
        className={classnames(styles.agentitem, this.props.agent.selected && styles.selected)}
        onClick={(e) => {
          e.stopPropagation();
          this.props.onItemClick(this.props.agent);
        }}
      >
        <Icon
          type={this.props.agent.icon.type}
          color={this.props.agent.icon.color}
          size={16}
        />
        <span className={styles.agentName}>
          {this.props.agent.name}
        </span>
        <span className={styles.agentDescription}>
          {this.props.agent.description}
        </span>
        <PromptNumber
          number={this.props.agent.unreadMessage}
          customClass={styles.unreadmessage}
        />
      </div>
    );
  }
}

Item.propTypes = {
  agent: React.PropTypes.shape({
    icon: React.PropTypes.shape({
      type: React.PropTypes.string,
      color: React.PropTypes.string,
    }).isRequired,
    name: React.PropTypes.string.isRequired,
    description: React.PropTypes.string.isRequired,
    unreadMessage: React.PropTypes.number.isRequired,
    selected: React.PropTypes.bool.isRequired,
  }),
  onItemClick: React.PropTypes.func,
};
Item.examples = {
  'Default Example': {
    description: 'Agents Container',
    component:
      <div>
        <Item
          agent={{
            icon: { type: icons.statusOnline, color: '#329fd9' },
            name: 'Allon (1 chat(s))',
            unreadMessage: 5,
            selected: true,
          }}
        />
        <Item
          agent={{
            icon: { type: icons.statusOnline, color: '#329fd9' },
            name: 'Allon (1 chat(s))',
            unreadMessage: 5,
            selected: false,
          }}
        />
      </div>
        ,
  },
};
export default Item;



