import { connect } from 'react-redux';
import _ from 'lodash';
import ChatHeader from '../../components/Agents/ChatHeader';
import { mapToChatHeader } from '../Business/agentChat';
import { setAnotherAgentStatus, logout, initCurrentAgent } from '../../actions/agent';
import { agentStatus, noSelectedForAgent } from '../../constants/enums';
import { updateAgent } from '../../actions/agents';
import * as selectors from '../../reducers/selectors';

const mapStateToProps = (state) => {
  if (_.isNull(selectors.getSelectedIdForAgents(state)) ||
      selectors.getSelectedIdForAgents(state) === noSelectedForAgent) {
    return {
      agent: {
        id: noSelectedForAgent,
        ifCurrentAgent: false,
        avatarLink: '',
        name: '',
        ifDisableKickOff: true,
        ifDisableSetAway: true,
      },
    };
  }
  return mapToChatHeader(state);
};

const mapDispatchToProps = dispatch => ({
  // bug: 18832
  onKickOff(agent) {
    if (agent.ifCurrentAgent) {
      dispatch(logout());
      return;
    }
    dispatch(setAnotherAgentStatus(agent.id, agentStatus.offline));
    dispatch(updateAgent(agent.id, true));
  },
  onSetAway(agent) {
    dispatch(setAnotherAgentStatus(agent.id, agentStatus.away));
    if (agent.ifCurrentAgent) {
      dispatch(initCurrentAgent({ status: agentStatus.away }));
    }
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ChatHeader);



