import React from 'react';
import classnames from 'classnames';
import GrayButton from '../Button/Button';
import styles from './agents.css';

import ModelKickOff from '../Modal/KickOff/KickOff';

class KickOff extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
    };
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }
  closeModal() {
    this.setState(
      { open: false }
    );
  }
  openModal() {
    this.setState(
      { open: true }
    );
  }
  render() {
    return (
      <div className={this.props.className}>
        <GrayButton
          text="Kick Off"
          type="default"
          className={classnames(styles.btnAction, this.props.disabled && styles.disable)}
          disabled={this.props.disabled}
          onClick={() => this.openModal()}
        />
        {this.state.open && (
          <ModelKickOff
            onKickOff={this.props.onKickOff}
            closeModal={this.closeModal}
          />
        )}
      </div>
    );
  }
}
KickOff.propTypes = {
  disabled: React.PropTypes.bool.isRequired,
  className: React.PropTypes.string.isRequired,
  onKickOff: React.PropTypes.func.isRequired,
};
export default KickOff;



