import React from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import styles from './agents.css';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';
import AgentList from './List';

class Department extends React.Component {
  shouldComponentUpdate(nextProps) {
    return nextProps.department.name !== this.props.department.name ||
           nextProps.department.selected !== this.props.department.selected ||
           nextProps.department.opened !== this.props.department.opened ||
           !_.isEqual(this.props.department.agents, nextProps.department.agents);
  }
  render() {
    return (
      <div
        className={classnames(styles.departmentsection,
                  this.props.department.selected && styles.selectedDpt,
                  this.props.department.opened && styles.opened)}
      >
        <div
          className={styles.departmentitem}
          onClick={(e) => {
            e.stopPropagation();
            this.props.onDepartmentClick(this.props.department);
          }}
        >
          <div className={styles.icon}>
            { this.props.department.agents.length > 0 &&
              <Icon type={icons.arrowDown} className={styles.depSpread} />}
          </div>
          <div className={styles.description}>
            <span>{this.props.department.name}</span>
            <span className={styles.departmentNum}>{`(Online: ${this.props.department.onlineNum}, Away: ${this.props.department.awayNum})`}</span>
          </div>
        </div>
        {
          this.props.department.opened && (
            <AgentList
              agents={this.props.department.agents}
              onItemClick={this.props.onItemClick}
            />
          )
        }
      </div>
    );
  }
}

Department.propTypes = {
  department: React.PropTypes.shape({
    name: React.PropTypes.string,
    onlineNum: React.PropTypes.number,
    awayNum: React.PropTypes.number,
    selected: React.PropTypes.bool,
    opened: React.PropTypes.bool,
    agents: React.PropTypes.arrayOf(React.PropTypes.shape({
      icon: React.PropTypes.shape({
        type: React.PropTypes.string,
        color: React.PropTypes.string,
      }),
      name: React.PropTypes.string,
      unreadMessage: React.PropTypes.number,
      selected: React.PropTypes.bool,
    })),
  }).isRequired,
  onDepartmentClick: React.PropTypes.func.isRequired,
  onItemClick: React.PropTypes.func.isRequired,
};
export default Department;



