import { connect } from 'react-redux';
import Agents from '../../components/Agents/AgentsLeft';
import mapToProps, { updateCheckUnreadMsgFlag } from '../Business/agents';
import { updateAgent,
  updateDepartment } from '../../actions/agents';

const mapStateToProps = state => mapToProps(state);

const mapDispatchToProps = dispatch => ({
  onDepartmentClick(info) {
    updateCheckUnreadMsgFlag(false);
    dispatch(updateDepartment(info.id));
  },
  onItemClick(info) {
    dispatch(updateAgent(info.id));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Agents);



