import React from 'react';
import classnames from 'classnames';
import CommonStyle from '../components/CommonResources/common.css';
import ResizeContainer from '../components/Resizer/ResizeContainer';
import Agents from './AgentsTab/Agents';
import AgentChatHeader from './AgentsTab/AgentChatHeader';
import AgentChatTextEditor from './SplitterAgentChatTextEditor';
import AgentChatContent from './AgentsTab/AgentChatContent';
import AgentChatAction from './AgentsTab/AgentChatAction';
import agentsTabStyle from '../components/Agents/agents.css';
import NoAgents from './AgentsTab/NoAgents';

class SplitterAgentChat extends React.Component {
  shouldComponentUpdate(nextProps) {
    return nextProps.agentNumber !== this.props.agentNumber ||
           nextProps.selectedAgentChatId !== this.props.selectedAgentChatId ||
           nextProps.agentsWidth !== this.props.agentsWidth;
  }
  render() {
    const {
      agentsWidth,
      agentNumber,
      onAgentsStopResize,
    } = this.props;
    return (
      <div className={CommonStyle.layoutContent} id="agents">
        {
        agentNumber > 1 ?
        (<ResizeContainer
          resizerPosition="left"
          width={agentsWidth}
          left_minWidth={190}
          left_maxWidth={350}
          right_minWidth={360}
          onStopResize={onAgentsStopResize}
        >
          <Agents />
          <div>
            <div className={classnames(agentsTabStyle.headerSection, CommonStyle.clearfix)}>
              <AgentChatHeader />
            </div>
            <div className={agentsTabStyle.chatWindowContainer}>

              <AgentChatContent />

              <div className={agentsTabStyle.toolbarSection}>
                <ul>
                  <AgentChatAction />
                </ul>
              </div>
              <div
                style={{
                  position: 'absolute',
                  width: '100%',
                  height: 175,
                  bottom: 0,
                  minHeight: 40,
                }}
              >
                <AgentChatTextEditor />
              </div>
            </div>
          </div>
        </ResizeContainer>) :
        (<NoAgents />)
      }
      </div>
    );
  }
}

SplitterAgentChat.propTypes = {
  agentNumber: React.PropTypes.number.isRequired,
  selectedAgentChatId: React.PropTypes.oneOfType([
    React.PropTypes.number,
    React.PropTypes.string,
  ]),
  agentsWidth: React.PropTypes.number.isRequired,
  onAgentsStopResize: React.PropTypes.func.isRequired,
};


export default SplitterAgentChat;



