import { connect } from 'react-redux';
import NoAgents from '../../components/Agents/NoAgents';
import * as common from '../Business/common';

const mapStateToProps = (state) => {
  const { isWeb,
        loginUrl,
        autoLoginUrl,
        addAgentsUrl } = common.getAgentUrls(state);
  return {
    siteInfo: {
      isWeb,
      loginUrl,
      autoLoginUrl,
      addAgentsUrl,
    },
  };
};

const mapDispatchToProps = () => ({
  addMoreAgents(siteInfo) {
    common.gotoAgentsList(siteInfo);
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(NoAgents);



