import React from 'react';
// import classnames from 'classnames';

import Modal from '../Modal';
import modalStyle from '../Modal.css';

class KickOff extends React.Component {
  constructor(props) {
    super(props);
    this.handleKickOff = this.handleKickOff.bind(this);
  }
  handleKickOff() {
    this.props.onKickOff();
    this.props.closeModal();
  }
  render() {
    return (
      <Modal
        closeModal={this.props.closeModal}
        title={'Comm100 Live Chat'}
        actions={{
          okProps: { text: 'Yes', eventClick: () => { this.handleKickOff(); } },
          cancelProps: { text: 'No' },
        }}
      >
        <span className={modalStyle.description}>
          If you kick off this agent, his ongoing chats will be ended.<br />
          Are you sure you want to continue?
        </span>
      </Modal>
    );
  }

}

KickOff.propTypes = {
  onKickOff: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
};

export default KickOff;




